import React from "react";
import Pagetitle from "../elements/Pagetitle";
import Timeline from "../elements/Timeline";

const educationData = [
  {
    id: 1,
    title: "Diploma in Software Engineering",
    years: "2019 - 2020",
    content:
      "I studied software engineering and graduated from Asia Pacific University",
  }
];

const experienceData = [
  {
    id: 1,
    title: "Full Stack Developer",
    years: "2020 - 2021",
    content:
      "Worked as a full stack developer at Antforce Sdn Bhd. In this company, I am using Jquery & PHP to develop projects",
  },
  {
    id: 2,
    title: "Full Stack Developer",
    years: "2021 - Present",
    content:
      "Working at Overwatch Technolgies Sdn Bhd as a full stack developer. In this company I am developing projects using React.js & Express.js",
  }
];

function Experiences() {
  return (
    <section id="experience">
      <div className="container">
        <Pagetitle title="Experience" />
        <div className="row">
          <div className="col-md-6">
            <div className="timeline edu bg-white rounded shadow-dark padding-30 overflow-hidden">
              {educationData.map((education) => (
                <Timeline key={education.id} education={education} />
              ))}
              <span className="line"></span>
            </div>
          </div>

          <div className="col-md-6">
            <div className="spacer d-md-none d-lg-none" data-height="30"></div>
            <div className="timeline exp bg-white rounded shadow-dark padding-30 overflow-hidden">
              {experienceData.map((experience) => (
                <Timeline key={experience.id} education={experience} />
              ))}
              <span className="line"></span>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Experiences;
