import React from "react";
import { Link } from "react-scroll";
import Pagetitle from "../elements/Pagetitle";
import Service from "../elements/Service";

const servicesData = [
  {
    id: 1,
    icon: "images/service-1.svg",
    title: "Frontend",
    content:
      "I have experience with HTML, CSS and JavaScript. For CSS, I use Bootstrap as my CSS framework." +
        " Also, for JavaScript, I use Jquery and React as my Js library",
    color: "#6C6CE5",
    contentColor: "light",
  },
  {
    id: 2,
    icon: "images/service-2.svg",
    title: "Backend",
    content:
      "On the back end, I have experience with PHP and Express.js. Currently I prefer to do Express.js for the project because Express.js will be better than PHP in terms of performance",
    color: "#F9D74C",
    contentColor: "dark",
  },
  {
    id: 3,
    icon: "images/service-3.svg",
    title: "Database Design",
    content:
      "I am using MySQL and MongoDB for my database. Usually, I choose my database based on the backend language I use.",
    color: "rgb(242 76 97)",
    contentColor: "light",
  },
];

function Services() {
  return (
    <section id="services">
      <div className="container">
        <Pagetitle title="Skillset" />
        <div className="row fix-spacing">
          {servicesData.map((service) => (
            <div className="col-md-4" key={service.id}>
              <Service service={service} />
            </div>
          ))}
        </div>
        <div className="mt-5 text-center">
          <p className="mb-0">
            Looking for a custom job?{" "}
            <Link
              className="colorpink pointer"
              to="section-contact"
              spy={true}
              smooth={true}
              duration={500}
            >
              Click here
            </Link>{" "}
            to contact me! ðŸ‘‹
          </p>
        </div>
      </div>
    </section>
  );
}

export default Services;
